<template>
  <div style="width:100%">
    <!-- 顶部toolbar -->
    <el-form ref="form1" :inline="true" class="toolbar" label-position="right"  :model="queryStr">
      <el-row >
        <el-form-item label="车辆品牌"  prop="bus_brand" >
          <el-input v-model.trim="queryStr.bus_brand" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="车辆系列" :inline="true" prop="bus_type" >
           <el-input v-model.trim="queryStr.bus_type" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="车辆类型" :inline="true" prop="bus_series" >
           <el-input v-model.trim="queryStr.bus_series" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click = "query(1)" >查询</el-button>
          <el-button type="primary" @click="newCar('form1')" >新建</el-button>
        </el-form-item>
      </el-row>
    </el-form>
    <!-- 列表 -->
    <el-table :data="tableData" border v-loading="loading">
       <el-table-column type="index" width="50" label="序号">
      </el-table-column>
			<el-table-column header-align="center" align="center" label="操作"  min-width="150">
				<template slot-scope="scope">
                    <el-button type="danger" size="mini" @click = "del_query(scope.row, scope.$index)">删除</el-button>
					<el-button type="primary" size="mini" @click = "edit_query(scope.row, scope.$index)">编辑</el-button>
          <!-- <el-button type="primary" size="mini" @click = "detail_query(scope.row, scope.$index)">查看</el-button> -->
				</template>
			</el-table-column>
			<el-table-column header-align="center" align="center" label="车辆品牌"  >
				<template slot-scope="scope">
					<span>{{scope.row.bus_brand}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="车辆系列"  >
				<template slot-scope="scope">
					<span>{{scope.row.bus_series}}</span>
				</template>
			</el-table-column>
			<el-table-column header-align="center" align="center" label="车辆类型"  >
				<template slot-scope="scope">
					<span>{{scope.row.bus_type}}</span>
				</template>
			</el-table-column>
    
    
		</el-table>
    <!-- 底部toolbar -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="prev, pager, next, jumper" @current-change="currentChange" :page-size="10" :total="queryStr.total" style="float:right;">
				</el-pagination>
			</el-col>
    <!-- 编辑弹出框 -->
      <el-dialog :title="title1" :visible.sync="dialogFormOneVisible" >
        <el-form label-width="120px" label-position="left" ref="form1" :model="form_one_data"  :rules="rules1" >
           <el-form-item label="车辆品牌" class="ml15" prop="bus_brand">
            <el-col :span="12">
              <el-input v-model="form_one_data.bus_brand"></el-input>
            </el-col>
          </el-form-item>
           <el-form-item label="车辆系列" class="ml15" prop="bus_series">
            <el-col :span="12">
              <el-input v-model="form_one_data.bus_series"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="车辆类型" class="ml15" prop="bus_type">
            <el-col :span="12">
              <el-input v-model="form_one_data.bus_type"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="状态" class="ml15" prop="status">
            <el-col :span="12">
              <el-radio v-model="form_one_data.status" label="1" >启用</el-radio>
              <el-radio v-model="form_one_data.status" label="2">停用</el-radio>
            </el-col>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormOneVisible = false">取 消</el-button>
          <el-button type="primary"  @click="addCarType">确 定</el-button>
        </div>
      </el-dialog>
  </div>
</template>
<script>
  import { carTypeList,addCarTypes,delBus,detailBus} from '@/api/api'
  import { tel_num } from '@/assets/utils/validate';
  export default {
    data(){
      return{
        edit:false,
        title1:'新建车型',
        queryStr: {
          bus_brand:'',
          bus_type:'',
          bus_series:'',
          total: 0,       // 总数
          page: 1,        // 当前分页
          size: 10,      // 每页条数
        },
        form_one_data:{
            bus_brand:"",
            bus_type:"",
            bus_series:"",
            status:'1',
        },
        rules1: {
          bus_brand: [
            { required: true, message: '请输入车辆品牌', trigger: 'blur' },
          ],
          bus_type: [
            { required: true, message: '请输入车辆类型', trigger: 'blur' },
          ],
          bus_series: [
            { required: true, message: '请输入车辆系列', trigger: 'blur' },
          ],
        },
        dialogFormOneVisible: false,
        dialogTableVisible: false,  
        tableData:[],
        loading: false,//loading
        centerDialogVisible: false,
      }
    },
    mounted(){
      this.query()
    
    },
    methods:{
      resetForm(formName) {
		        this.$refs[formName].resetFields();
      },
      newCar(formName){
        
          this.form_one_data = {'status':'1'}
        this.dialogFormOneVisible = true;
      },
      // 查询
      query(run_type) {
          let params = {
              page: this.queryStr.page,
              limit: this.queryStr.limit,
              bus_brand: this.queryStr.bus_brand,
              bus_type: this.queryStr.bus_type,
              bus_series:this.queryStr.bus_series
          };
          carTypeList(params).then((res) => {
            this.queryStr.total = Number(res.data.data.total);
            this.tableData = res.data.data.data;
          });
        
      },
      //新建品牌车型
      addCarType() {
        this.$refs['form1'].validate((valid) => {
            if (valid) {
                let params = this.form_one_data;
                addCarTypes(params).then((res) => {
                    this.$message.success('执行成功!');
                    this.dialogFormOneVisible = false;
                    this.form_one_data = {};
                    this.query();
                });
            } else {
              console.log('error submit!!');
              return false;
            }
        });
          
      },
      del_query(val,index){ //删除
          let id = val.id;
          let params = {
            'id':id,
          }
          delBus(params).then((res) => {
              this.$message.success('【删除】操作成功!');
              this.query();
          });
      },
      resetForm(form){
        this.$refs['form1'].resetFields();
      },
      // 分页器
      currentChange(val) {
            this.queryStr.page = val;
            this.query();
      },
       // 修改 yjq
      edit_query(val, index){
          let id = val.id;
          let params = {
            'id':id,
          }
          detailBus(params).then((res) => {
              this.title1 = "编辑车型";
              this.form_one_data = res.data.data;
              this.dialogFormOneVisible = true;
          });
      },
      // 时间戳的转换
      changeTime(time){
          let year = new Date((time)).getFullYear();
          let month = new Date((time)).getMonth()+1 < 10?'0'+(new Date((time)).getMonth()+1):(new Date((time)).getMonth()+1);
          let date = new Date((time)).getDate() < 10 ?'0'+new Date((time)).getDate():new Date((time)).getDate();
          let hours = new Date((time)).getHours() < 10 ?'0'+new Date((time)).getHours():new Date((time)).getHours();
          let minutes = new Date((time)).getMinutes() < 10 ?'0'+new Date((time)).getMinutes():new Date((time)).getMinutes();
          let seconds = new Date((time)).getSeconds() < 10 ?'0'+new Date((time)).getSeconds():new Date((time)).getSeconds();
          return year+'-'+month+'-'+date+' '+hours+':'+minutes+':'+seconds
      }
    }
  }
</script>
<style scoped lang="scss">
  .el-radio-group {
		display: -webkit-flex;
		flex-wrap: wrap;
		label {
			flex-basis: 25%;
			margin-left:0 ;
			line-height: 30px;
		}
	}

  
</style>

<style>
.select  .el-input__inner{
    width:203px;
  }
</style>





